.resources_title_container{display:flex;flex-direction:column;align-items:center;justify-content:center;margin:auto;height:40vh}.resourcesTitle{color:white;justify-self:center;align-self:center;font-weight:300;font-size:64px}.navigation_button{padding-top:6px;padding-right:12px;padding-bottom:6px;padding-left:12px;border-radius:16px;background:rgba(255,255,255,0.1);color:white !important;border:none}.button_container{display:flex;grid-gap:30px;gap:30px}.img_container{display:flex;height:100%}.img_container img{position:absolute;z-index:-1}.img_container .nines{width:70%;height:80%;left:20%;bottom:28%}.img_container .eclipse{width:30%;height:30%;border:none;outline:none;left:30%;top:20%;background:radial-gradient(110.29% 110.29% at 97.66% 31.26%, rgba(233,237,237,0.8) 0%, rgba(252,0,252,0.64) 20.11%, rgba(76,210,178,0.336) 44.03%, rgba(11,12,11,0.424) 74.02%, rgba(0,0,0,0.8) 100%);opacity:0.9;-webkit-filter:blur(143.294px);filter:blur(143.294px)}.header_container{overflow:hidden}

.recomendation_header{margin-top:30px;text-align:center}.recomendation_header h2{font-style:normal;font-weight:lighter;color:white}.recomendation_header p{margin-top:50px;font-size:28px;color:#ffffffcc;font-weight:400}.WAVES{position:absolute;z-index:-1;top:150%;width:100%;height:100%}.eclipse{z-index:-1;position:absolute;width:30%;height:30%;border:none;outline:none;left:0;bottom:-1000;background:radial-gradient(110.29% 110.29% at 97.66% 31.26%, rgba(233,237,237,0.8) 0%, rgba(252,0,252,0.64) 20.11%, rgba(76,210,178,0.336) 44.03%, rgba(11,12,11,0.424) 74.02%, rgba(0,0,0,0.8) 100%);opacity:0.9;-webkit-filter:blur(143.294px);filter:blur(143.294px)}

.text_utility_section{margin-top:15%;text-align:center;width:90%}.text_utility_section h2{color:white;font-weight:lighter;font-size:40px}.text_utility_section p{color:#ffffffcc;margin-top:56px;font-size:28px}.creative_utility_section{margin-bottom:15%}

.details_headline{text-align:center}.details_headline h1{color:white;font-weight:lighter}.details_headline p{color:white}.details_img_wrapper{display:flex;justify-content:center;border-top:1px solid white;border-bottom:1px solid white}.details_img_wrapper img{max-width:600px;max-height:390px}.text_container{margin-top:70px}.text_container h2{color:white;font-weight:700}.text_container p{color:white;margin-top:70px}

