@import "../global.scss";

.footer {
  .container {
    max-width: 1200px;
    width: 100%;
    a,
    p,
    li {
      font-size: 1.2rem !important;
    }
    a {
      color: white !important;
      &:hover {
        color: #3774d0 !important;
      }
    }
  }
  .footer-fixed {
    background-color: $primary-color;

    .footer-row {
      display: flex;
      justify-content: space-between;
      align-items: center;

      .footer-links {
        color: white;
        text-decoration: none;
        list-style: none;
        font-weight: 400;
        font-size: 24px;
        line-height: 29px;
        margin-top: 80px;

        li {
          margin-bottom: 24px;
        }
      }

      .footer-column {
        .footer-contact {
          li {
            color: white;
            list-style: none;
            font-weight: 400;
            font-size: 24px;
            line-height: 29px;
            margin-bottom: 24px;

            a {
              color: white;
            }
          }
        }
        .social-icons {
          .icons {
            margin-right: 10px;
            color: $primary-color5;
          }
        }
      }

      .footer-logo {
        img {
          width: 180px;
          height: auto;
        }
      }
    }

    .footer-copyright {
      margin-top: 40px;
      margin-bottom: 0;
      text-align: center;
      font-weight: normal;
      font-size: 15px !important;
      line-height: 22px;
      color: $primary-color5;
    }
  }
}
